/** @format */
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700;900&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Lato', sans-serif;
}

html,
body {
  min-height: 100%;
}

body {
  background-image: linear-gradient(to top, #537895 0%, #09203f 100%);
  background-repeat: no-repeat;
  min-width: 100%;
}

.container {
  /* margin: auto;
  max-width: 1200px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.container h1 {
  color: #e1e1e1;
  font-size: 3.5em;
  font-weight: 900;
}

.container p {
  color: #e1e1e1;
  font-size: 2.5em;
  font-weight: 900;
}

.form-nomination-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2em;
}

.text-form-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 660px;
}

.text-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1em;
}

.form-container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.button-container {
  margin-left: 10px;
}

.movie-icon {
  color: #e1e1e1;
  font-size: 4.5em;
  margin-left: 8px;
}

form {
  position: relative;
}

.search-icon {
  position: absolute;
  top: 22%;
  right: 3%;
  color: #e1e1e1;
  font-size: 1.5em;
}

.search-icon:hover {
  cursor: pointer;
}

input {
  width: 28em;
  height: 3em;
  border-radius: 0.3em;
  background-color: #47657c;
  outline: none;
  border: none;
  color: #e1e1e1;
  font-size: 1em;
  font-weight: 800;
  padding: 1.1em 1em;
}

input::placeholder {
  color: #e1e1e1;
  opacity: 0.7;
}

input:focus {
  background-color: #537895;
}

button {
  background: none;
  outline: none;
}

.disabled {
  color: #6e6d6d;
  font-size: 1.8em;
  border: none;
}

.disabled:hover {
  cursor: default;
}

.next {
  color: #e1e1e1;
  font-size: 1.8em;
  border: none;
}

.previous {
  color: #e1e1e1;
  font-size: 1.8em;
  border: none;
}

.next:hover {
  cursor: pointer;
}

.previous:hover {
  cursor: pointer;
}

.nomination-container {
  color: #e1e1e1;
  width: 450px;
  height: 225px;
  border-radius: 5px;
  box-shadow: 0 0 3px #ccc;
  background-color: #47657c;
  padding: 1em 1em;
  margin: 0.5em 2em;
}

.nomination-header {
  font-size: 2.5em;
  font-weight: 900;
}

.nomination-box-container {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.nomination {
  display: flex;
  font-size: 1.1em;
  min-width: 380px;
  margin: 0.25em 0;
}

.long-nomination-title {
  width: 380px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1.1em;
  margin: 0.25em 0;
}

.close-icon {
  font-size: 1.3em;
  margin-left: 0.3em;
}

.close-icon:hover {
  cursor: pointer;
}

.movies-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2em;
  margin-top: 2.5em;
  color: #e1e1e1;
}

.movies {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.movie-img-container {
  position: relative;
}

.movie-img {
  height: 25em;
  width: 20em;
}

.movie-img-opacity {
  height: 25em;
  width: 20em;
  opacity: 0.3;
}

.star-icon-active {
  position: absolute;
  top: 1%;
  right: 1%;
  font-size: 1.8em;
  color: rgb(248, 248, 66);
}

.star-icon-inactive {
  position: absolute;
  top: 1%;
  right: 1%;
  font-size: 1.8em;
  color: #e1e1e1;
}

.star-icon-inactive:hover,
.star-icon-active:hover {
  cursor: pointer;
}

.long-title {
  width: 300px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.title {
  text-overflow: none;
  margin-top: 0.3em;
}

.year {
  margin: 0.3em 0 1em 0;
}

.no-movie {
  color: #e1e1e1;
  font-size: 1.5em;
}

.too-many-movies {
  color: #e1e1e1;
  font-size: 1.5em;
}

.skeleon-container {
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.popup-container {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);

  display: flex;
  justify-content: center;
  align-items: center;
}

.popup {
  position: relative;
  bottom: 25%;
  padding: 32px;
  width: 100%;
  max-width: 720px;
  background-color: #47657c;
  color: #e1e1e1;
  border-radius: 5px;
}

.pop-close-icon {
  position: absolute;
  top: 2%;
  right: 2%;
  color: #e1e1e1;
  font-size: 1.5em;
}
.pop-close-icon:hover {
  cursor: pointer;
}

.popup-header {
  font-size: 1.5em;
  font-weight: 900;
  margin-bottom: 0.5em;
}

.title-header-container {
  display: flex;
  justify-content: flex-start;
  font-weight: 900;
}

.t-title {
  min-width: 170px;
  margin-left: 5.3em;
}

.t-year {
  min-width: 200px;
}

.t-rating {
  min-width: 200px;
}

.popupdata-container {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin: 0.5em 0;
}

.popupdata-container img {
  width: 80px;
  height: 130px;
}

.p-title {
  width: 300px;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 0.5em;
}

.p-year {
  min-width: 200px;
}
.p-rating {
  min-width: 200px;
}

@media (min-width: 320px) and (max-width: 480px) {
  .container h1 {
    font-size: 2.5em;
  }

  .container p {
    font-size: 1.5em;
  }

  .movie-icon {
    font-size: 2.5em;
  }

  .text-form-container {
    width: 300px;
  }

  input {
    width: 14em;
    height: 1.5em;
    margin-bottom: 0.5em;
  }

  input::placeholder {
    color: #e1e1e1;
    opacity: 0.7;
    font-size: 0.7em;
  }

  .search-icon {
    top: 17%;
  }

  .form-nomination-container {
    flex-direction: column;
  }

  .nomination-container {
    width: 300px;
    height: 162px;
  }

  .nomination-header {
    font-size: 1.5em;
    margin: 0em 0 0.5em 0;
  }

  .nomination-box-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .nomination {
    display: flex;
    font-size: 0.7em;
    min-width: 250px;
    margin: 0.15em 0;
  }

  .long-nomination-title {
    width: 250px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 0.7em;
    margin: 0.15em 0;
  }

  .close-icon {
    font-size: 0.8em;
  }

  .movies-container {
    grid-template-columns: repeat(1, 1fr);
  }

  .movie-img {
    height: 18.5em;
    width: 15em;
  }

  .movie-img-opacity {
    height: 18.5em;
    width: 15em;
    opacity: 0.3;
  }

  .popup-container {
    position: fixed;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .popup {
    position: relative;
    bottom: 39%;
    padding: 32px;
    width: 100%;
    max-width: 300px;
    background-color: #47657c;
    color: #e1e1e1;
    border-radius: 5px;
  }

  .pop-close-icon {
    position: absolute;
    top: 2%;
    right: 2%;
    color: #e1e1e1;
    font-size: 1.5em;
  }
  .pop-close-icon:hover {
    cursor: pointer;
  }

  .popup-header {
    font-size: 1.5em;
    font-weight: 900;
    margin-bottom: 0.5em;
  }

  .title-header-container {
    display: flex;
    justify-content: flex-start;
    font-weight: 900;
  }

  .t-title {
    margin-left: 5.3em;
  }

  .t-year {
    display: none;
  }

  .t-rating {
    display: none;
  }

  .popupdata-container {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    margin: 0.5em 0;
  }

  .popupdata-container img {
    width: 80px;
    height: 130px;
  }

  .p-title {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    margin-left: 0.5em;
  }

  .p-year {
    display: none;
  }
  .p-rating {
    display: none;
  }
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container h1 {
    font-size: 2.5em;
  }

  .container p {
    font-size: 1.5em;
  }

  .movie-icon {
    font-size: 2.5em;
  }

  .text-form-container {
    width: 300px;
  }

  input {
    width: 14em;
    height: 1.5em;
    margin-bottom: 0.5em;
  }

  input::placeholder {
    color: #e1e1e1;
    opacity: 0.7;
    font-size: 0.7em;
  }

  .search-icon {
    top: 17%;
  }

  .nomination-container {
    width: 300px;
    height: 162px;
  }

  .nomination-header {
    font-size: 1.5em;
    margin: 0em 0 0.5em 0;
  }

  .nomination-box-container {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }

  .nomination {
    display: flex;
    font-size: 0.7em;
    min-width: 250px;
    margin: 0.15em 0;
  }

  .long-nomination-title {
    width: 250px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    font-size: 0.7em;
    margin: 0.15em 0;
  }

  .close-icon {
    font-size: 0.8em;
  }

  .movies-container {
    grid-template-columns: repeat(3, 1fr);
  }

  .movie-img {
    height: 14.5em;
    width: 11em;
  }

  .movie-img-opacity {
    height: 14.5em;
    width: 11em;
    opacity: 0.3;
  }

  .popup {
    bottom: 10%;
  }
}
